import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ApiserviceService} from '../services/apiservice.service';
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {

  constructor(private apiService : ApiserviceService,private router : Router) { }
  fname:any
  lname:any
  email:any
  pass:any
  mbnum:any
  dob:any
  
  username:any;
  password:any;

  ngOnInit(): void {
  }

  loginUser(){
    this.username = document.getElementsByTagName('input')[7].value;
    this.password = document.getElementsByTagName('input')[8].value;
    console.log("In login");
    const data = {
      mobileNumber : this.username,
      password : this.password
    }
    console.log('Login data is ',data);
    const res = this.apiService.postData('login',data).subscribe(result=>{
      console.log("Login result is ",result);
      if(result){
        console.log("Login success");
        localStorage.setItem('user_id','5');
        this.router.navigateByUrl('');
      }else{
        console.log("login failed");
        alert("Please Check Credentials");
      }
    })


  }

  registerUser(){
    this.fname = document.getElementsByTagName('input')[1].value;
    this.lname = document.getElementsByTagName('input')[2].value;
    this.email = document.getElementsByTagName('input')[3].value;
    this.pass = document.getElementsByTagName('input')[4].value;
    this.mbnum = document.getElementsByTagName('input')[5].value;
    this.dob = document.getElementsByTagName('input')[6].value;
    console.log("user register");
    const data = {
      firstName : this.fname,
      lastName : this.lname,
      email: this.email,
      mobileNumber : this.mbnum,
      password: this.pass,
      dateOfBirth : this.dob

    }
    console.log('user data is ',data);
    const res = this.apiService.postData('registration',data).subscribe(result =>{
      console.log(result);
    })
  }

}
