import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ApiserviceService} from '../services/apiservice.service';
@Component({
  selector: 'app-admin-login',
  templateUrl: './admin-login.component.html',
  styleUrls: ['./admin-login.component.scss']
})
export class AdminLoginComponent implements OnInit {

  constructor(private apiService:ApiserviceService,private router:Router) { }
  username:any;
  password:any;
  ngOnInit(): void {
  }
  adminLogin(){
    console.log("admin login func");
    this.username = document.getElementsByTagName('input')[0].value
    this.password = document.getElementsByTagName('input')[1].value
    console.log('username is :',this.username,'password is ',this.password);
    const res = this.apiService.getData('getlogindetails/'+this.username);
    res.subscribe(result => {
      console.log("admin login details are :: ",result);
      if(result[0]['admin_username']==this.username && result[0]['password']==this.password){
        this.router.navigateByUrl('admin');
      }else{
        alert("Wrong Login Credentials");
      }
    })
  }

}
