import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ApiserviceService } from '../services/apiservice.service';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.scss']
})
export class NavbarComponent implements OnInit {

  itemcount:number=0;
  cartitems: any;
  userid=localStorage.getItem('user_id');
  showlogin:boolean=false;
  constructor(
    private apiService : ApiserviceService,private router : Router
    
  ) { }

  ngOnInit(): void {
    this.getitemscount();
  }

  
  getitemscount(){
    console.log("In get items count");
    this.itemcount=0;
    const res = this.apiService.getData('getcartitems/'+this.userid);
    res.subscribe(results=>{
      console.log("cart items are ",results);
      this.cartitems=results;
      for(var i=0;i<this.cartitems.length;i++){
        this.itemcount = this.itemcount + this.cartitems[i]['quantity'];
      }
     
    })
    
  }

  toggleLoginLogout(){

    if(localStorage.getItem('user_id')!= null || localStorage.getItem('user_id')!= ''){
      this.showlogin = false;
    }else{
      this.showlogin = true;
    }

  }

  logout(){
    console.log("In logout");
    localStorage.clear();
    //localStorage.setItem('user_id','5');
    this.router.navigateByUrl('login');
    this.showlogin = true;
  }

  searchItem(){
    console.log("In search Item");
  }

 

}
